console.log("yoooo");

type LedgerResponse = {
  type: "pong";
};

const reply = (msg: LedgerResponse) => {
  window.parent.postMessage(msg, "*");
};

reply({
  type: "pong"
});

export function main(e: MessageEvent<any>) {
  console.log(e);
  reply({
    type: "pong"
  });
}

window.addEventListener("message", main);
